/* Add this CSS to your project's stylesheet */

.navbar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
  }
  
  .navbar li {
    float: left;
  }
  
  .navbar li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  .navbar li a.active {
    background-color: #555; /* Active link color */
  }
  
  .navbar li a:hover:not(.active) {
    background-color: #111;
  }




